/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.relation {
  position: relative;
}

.relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.relation__offset {
  height: 0;
}

.relation__offset-1-1 {
  padding-top: 100%;
}

.relation__offset-16-9 {
  padding-top: 56.25%;
}

.relation__offset-3-2 {
  padding-top: 67%;
}

body {
  background: #27e9f0;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 30px;
}

.row {
  border: 1px solid #f90;
}

.row .relation__offset {
  padding-top: 56.25%;
}

.big {
  background: #fffd73;
  flex: 1 1 100%;
  width: 40%;
}

.product {
  flex: 1 1 100%;
  width: 30%;
  display: flex;
  flex-direction: column;
}

.product1 {
  order: -1;
}

.productItem {
  flex: 1 1 50%;
  transition: all 0.3s ease-out;
}

.productItem:hover {
  opacity: 0.2;
}

.item1 {
  background: #73c9ff;
}

.item2 {
  background: #7373ff;
}

.item3 {
  background: #73c9ff;
}

.item4 {
  background: #7373ff;
}

.profit {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.profit .item {
  margin-left: 15px;
  margin-right: 15px;
  box-sizing: border-box;
  width: calc(33.333333333333336% - 30px);
  text-align: center;
}

@media screen and (max-width: 780px) {
  .relation__offset {
    padding-top: 100%;
  }

  .big {
    width: 70%;
    background: #00ff00;
  }

  .product {
    flex: 1 1 50%;
  }

  .row .relation__offset {
    padding-top: 100%;
  }

  .product {
    flex: 1 1 50%;
  }

  .product1 {
    order: 0;
  }

  .profit {
    justify-content: center;
  }

  .profit .item {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 560px) {
  .row .relation__offset {
    display: none;
  }

  .relation__content {
    flex-wrap: nowrap;
    position: static;
  }

  .big,
  .product {
    width: auto;
  }
}